@import './styles.scss';

.profession {
  margin-bottom: 192px;
}

.profession__header {
  display: flex;
  font-size: 18px;
  line-height: 24px;
}

.profession__question {
  line-height: 40px;
}

.profession__question {
  width: 15%;
}

.profession__menu {
  width: 85%;
  display: flex;
  justify-content: flex-end;
}

.profession__menu-item,
.profession__menu-item-active {
  color: $mainColor;
  padding: 8px 16px;
  border-radius: 36px;
  transition: 0.3s;
}

.profession__menu-item-active {
  background: $mainColor;
  color: $whiteColor;
}

.profession__title {
  font-size: 160px;
  line-height: 144px;
  letter-spacing: -0.03em;
  margin-top: 24px;
  margin-bottom: 96px;
}

.profession__content {
  display: flex;
  justify-content: space-between;
}

.profession__side-bar {
  width: 33.33%;
  max-width: 440px;
  display: flex;
  flex-direction: column;
}

.profession__side-bar-text-wrapper:nth-child(2) {
  margin-bottom: 24px;
}

.profession__side-bar-image-tools-wrapper {
  max-width: 340px;
}

.profession__side-bar-tools {
  width: 100%;
  object-fit: contain;
}

.profession__side-bar-text {
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 32px;
}

.profession__side-bar-vacancy,
.profession__side-bar-vacancy-mobile {
  display: block;
  color: $mainColor;
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 32px;
  margin-top: auto;
}

.profession__side-bar-vacancy-mobile {
  display: none;
}

.profession__side-bar-image-wrapper {
  max-width: 416px;

  & > .profession__side-bar-image {
    width: 100%;
  }
}

.profession__side-bar-vacancy-value-arrow {
  display: inline-block;
  width: 14px;
  height: 9px;
  background: url('../images/management/arrow-right.svg');
}

.profession__main-content {
  width: 66.66%;
}

.profession__filter-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 64px;
  font-size: 18px;
  line-height: 24px;
}

.profession__checkbox-container,
.profession__checkbox-container-mobile {
  display: flex;
}

.profession__checkbox-container-mobile {
  display: none;
}

.profession__filter-item {
  width: 100%;
  max-width: 304px;
  margin-right: 16px;
  border-bottom: 1px solid $blackColorOpacity-05;
  position: relative;
  cursor: pointer;

  &:last-child {
    margin-right: 0;
  }
}

.profession__filter-item-select {
  display: flex;
  justify-content: space-between;
}

.profession__filter-item-select-list {
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  background-color: $bodyBackgraund;
}

.profession__filter-item-select-list-item {
  cursor: pointer;
}

.profession__filter-item-title {
  font-size: 12px;
  line-height: 12px;
  color: $blackColorOpacity-05;
  margin-bottom: 4px;
}

.profession__filter-input {
  display: none;
}

.profession__filter-checbox-wrapper {
  display: block;
  margin-right: 16px;
  padding-left: 24px;
  width: max-content;
  cursor: pointer;
  position: relative;
}

.profession__filter-checbox-value {
  display: block;
  position: absolute;
  left: 0;
  top: 2px;
  width: 16px;
  height: 16px;
  border: 1px solid $mainColor;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: url('../images/management/check.svg') center / cover no-repeat;
    opacity: 0;
    transition: 0.2s;
  }
}

.profession__filter-input:checked + .profession__filter-checbox-value::before {
  opacity: 1;
}

.profession__filter-reset {
  margin-left: auto;
  font-size: 18px;
  line-height: 24px;
  color: #969696;
  background: transparent;
}

.profession__job-item {
  display: flex;
  font-size: 18px;
  line-height: 24px;
  border-top: 1px solid #969696;
  padding-bottom: 32px;
  padding-top: 8px;
  transition: 0.2s;

  &:first-child {
    border-top: 1px solid transparent;
  }

  &:hover .profession__job-title {
    color: rgba(227, 18, 53, 1);
  }

  &:hover {
    border-top: 1px solid rgba(227, 18, 53, 1);
  }

  &:hover .profession__more-info {
    opacity: 1;
  }
}

.profession__job-title {
  color: $mainColor;
  transition: 0.2s;
}

.profession__job-department {
  font-size: 12px;
  line-height: 12px;
  color: $blackColorOpacity-05;
}

.profession__job-title-container {
  margin-right: auto;
  position: relative;
}

.profession__more-info {
  position: absolute;
  right: -100%;
  bottom: -16px;
  width: 195px;
  color: $mainColor;
  font-size: 12px;
  line-height: 12px;
  border-radius: 36px;
  background: $whiteColor;
  padding: 10px 36px 10px 16px;
  transition: 0.2s;
  opacity: 0;

  &::before {
    content: '';
    position: absolute;
    top: calc(50% - 4.5px);
    right: 17px;
    width: 14px;
    height: 9px;
    background: url('../images/management/arrow-right.svg') center / cover
      no-repeat;
  }
}

.profession__job-price-container,
.profession__location-container {
  width: 100%;
  max-width: 310px;
  display: flex;
  flex-direction: column;
}

.profession__job-expiriens,
.profession__location {
  margin-top: auto;
}

// Styles for 1440px
@media (max-width: 1440px) {
  .profession__header {
    font-size: 16px;
    line-height: 20px;
  }

  .profession__title {
    font-size: 124px;
    line-height: 112px;
    margin-bottom: 88px;
  }

  .profession__question {
    line-height: 36px;
  }

  .profession__side-bar-text,
  .profession__side-bar-vacancy,
  .profession__filter-wrapper,
  .profession__job-item,
  .profession__filter-reset {
    font-size: 16px;
    line-height: 20px;
  }

  .profession__filter-wrapper {
    margin-bottom: 48px;
  }

  .profession__filter-item {
    max-width: 224px;
  }

  .profession__job-item {
    padding-bottom: 24px;
  }

  .profession__side-bar-text,
  .profession__side-bar-vacancy {
    margin-bottom: 24px;
  }

  .profession__side-bar-text-wrapper > .profession__side-bar-text:nth-child(2) {
    margin-bottom: 16px;
  }

  .profession__job-price-container,
  .profession__location-container {
    max-width: 240px;
  }
}

// Styles for 1024px
@media (max-width: 1330px) {
  .profession__side-bar {
    max-width: 302px;
  }

  .profession__side-bar-image-tools-wrapper > img {
    display: none;
  }

  .profession__side-bar-image-tools-wrapper {
    background: url('../images/management/tools1024.svg') left / contain
      no-repeat;
    min-height: 104px;
  }

  .profession__filter-item {
    max-width: 240px;
  }

  .profession__checkbox-container {
    display: none;
  }

  .profession__checkbox-container-mobile {
    display: flex;
    margin-bottom: 40px;
  }

  .profession__filter-wrapper {
    margin-bottom: 8px;
  }

  .profession__filter-checbox-wrapper {
    width: 100%;
    max-width: 240px;
    margin-right: 16px;
  }

  .profession__job-price-container,
  .profession__location-container {
    max-width: 170px;
  }
}

@media (max-width: 1150px) {
  .profession {
    margin-bottom: 144px;
  }

  .profession__header {
    font-size: 16px;
    line-height: 20px;
    flex-direction: column;
  }

  .profession__title {
    font-size: 124px;
    line-height: 112px;
    margin-top: 12px;
    margin-bottom: 64px;
  }

  .profession__question {
    width: 100%;
    line-height: 20px;
    margin-bottom: 24px;
  }

  .profession__menu {
    width: max-content;
    justify-content: flex-start;
  }
}

// Styles for 768px
@media (max-width: 991px) {
  .profession__content {
    flex-direction: column;
  }

  .profession__side-bar-vacancy {
    display: none;
  }

  .profession__side-bar-vacancy-mobile {
    display: block;
    margin-top: auto;
    margin-bottom: 0;
    font-size: 16px;
    line-height: 20px;
  }

  .profession__side-bar {
    width: 100%;
    max-width: 100%;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 64px;
  }

  .profession__side-bar-text-wrapper {
    width: 60%;
    max-width: 448px;
    display: flex;
    flex-direction: column;
  }

  .profession__side-bar-image-tools-wrapper {
    background: url('../images/management/tools-tablet.svg') left / contain
      no-repeat;
    min-height: 64px;
  }

  .profession__side-bar-image-wrapper {
    width: 40%;
    max-width: 280px;
    display: flex;
  }

  .profession__main-content {
    width: 100%;
  }
}

@media (max-width: 850px) {
  .profession {
    margin-bottom: 128px;
  }

  .profession__title {
    font-size: 88px;
    line-height: 80px;
    margin-top: 12px;
    margin-bottom: 64px;
  }
}

// Styles for 320px
@media (max-width: 600px) {
  .profession {
    margin-bottom: 64px;
  }

  .profession__title {
    font-size: 36px;
    line-height: 38px;
    margin-top: 32px;
    margin-bottom: 16px;
  }

  .profession__header {
    font-size: 14px;
    line-height: 16px;
  }

  .profession__question {
    margin-bottom: 16px;
  }
}
